}

.row{
	margin: 0;
}

.row{
    margin: 0px;
}

#signInContainer {
    margin-top: 10vh;
    background-color: white;
    border-radius: 10px;
    padding: 20px;
}

#registerText { 
    margin-top: 50px;
}

#logInBtnContainer{
	display: inline-block;
}
#logInErrorBox{
	display: inline-block;
}

.errIcon{
	width: 30px;
}

.errorMessage{
	padding: 5px 10px;
	border-radius: 15px;
	background-color: #75AD49;
	color: white;
	box-shadow: 1px 1px 4px 2px #ccc;

}

#errText{
	padding-left: 10px;
}

.header {
    background-color: #75AD49;
    width: 100%;
    height: 30vh;
    text-align: center;
		padding-top: 20px;    
		position: relative;
  }
  
  .arch{
    background-color: #E9FFDE;
    width: 100%;
		height: 80%;
    border-radius: 100% / 25%;
    border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
		position: absolute;
    bottom: 0;
  }

	.profileBtnContainer{
		position: absolute;
		text-align: center;
		top: 1%;
	}

	.profileBtn{
		width: 25vw;
    height: 25vw;
    max-width: 200px;
    max-height: 200px;
    min-width: 150px;
		min-height: 150px;
		border-radius: 50%;
		border: none;
		padding:0;
	}

	.profileBtn:hover{
			-webkit-transform: scale(1.05, 1.05);
			        transform: scale(1.05, 1.05);
			-webkit-transition: all .2s ease;
			transition: all .2s ease;
	}

	.profileImage{
		width: 100%;
	}
  
  
  .signOutBtn{
		float: right;
		color: white;
		background-color: rgb(249, 145, 34);
		border-color: rgb(249, 145, 34);
		box-shadow: 1px 1px 2px 1px rgb(75, 75, 75);
	}
	
	.signOutBtn:hover{
		background-color: rgb(255, 170, 79);
		border-color: rgb(255, 170, 79);
	}

