}

.roundBtn{
    width: 12vw;
    height: 12vw;
    max-width: 100px;
    max-height: 100px;
    min-width: 60px;
    min-height: 60px;
    background-color: rgb(249, 145, 34);
    border-radius: 50%;
		border-color: rgb(249, 145, 34);
		box-shadow: 1px 1px 4px 2px #ccc;
}

.roundBtn:hover{
    border-color: rgb(255, 214, 169);
    background-color: rgb(255, 214, 169);
    -webkit-transform: scale(1.25, 1.25);
            transform: scale(1.25, 1.25);
		-webkit-transition: all .2s ease;
		transition: all .2s ease;

}
.inventoryBody{
    position: relative;
    background-color: white;
    padding: 10px;
    padding-top: 20px;
    border-radius: 15px;
}

.itemBtn{
    float:right;
}

.title{
    text-align: center;
		padding-bottom: 20px;
		font-family: 'Open Sans', sans-serif;
}


.delItemBtn{
    width: 8vw;
    height: 3vw;
    max-width: 100px;
    max-height: 40px;
    min-width: 50px;
    min-height: 40px;
    background-color: rgb(249, 145, 34);
    border-radius: 15px;
    border-color: rgb(249, 145, 34);
		box-shadow: 1px 1px 2px 1px rgb(189, 189, 189);
}

.delItemBtn:hover{
    border-color: red;
    background-color: red;
}


.addItemBtn{
    width: 10vw;
    height: 10vw;
    max-width: 80px;
    max-height: 80px;
    min-width: 60px;
    min-height: 60px;
    background-color: rgb(249, 145, 34);
    border-radius: 50%;
    border-color: rgb(249, 145, 34);
		margin-bottom: 15px;
		box-shadow: 1px 1px 2px 1px rgb(189, 189, 189);
}

.addItemBtn:hover{
    border-color: rgb(255, 214, 169);
    background-color: rgb(255, 214, 169);
    -webkit-transform: scale(1.25, 1.25);
            transform: scale(1.25, 1.25);
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
}

